.tools {
    width: 100px;
    float: left;
    padding: 5px;
}

.tools li {
    padding: 5px;
    list-style: none;
}

.tools li:hover {
    color: cornflowerblue;
    cursor: pointer;
}

.tools li.marked {
    background-color: cornflowerblue;
    color: white;
}

#drawArea {
    border: 1px solid #000000;
    background-color: lightgrey;
}

.background-color-list {
    list-style-type: none;
    display: block;
}

.colorForm {
    display: flex;
    align-items: normal;
}

.color-radioBttn {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    margin-right: 5px;
    cursor: pointer;
}

.lblRadio {
    margin: 0;
    cursor: pointer;
}

.drawing {
    background-color: gray;
    height: 500px;
    width: 500px;
}

.move-cursor {
    cursor: move;
}

#eventOutput {
    height: 305px;
    width: 1006px;
}

.normal-cursor {
    cursor: default;
}

.canvas-selection {
    cursor: pointer;
}

.label {
    cursor: pointer;
    display: inline-block;
    width: 50px;
    position: relative;
}

.drawing:hover {
    background-color: red;
}

#popupMenu {
    position: fixed;
    z-index: 10000;
    width: 150px;
    background: #1b1a1a;
    border-radius: 5px;
    transform: scale(0);
    transform-origin: top left;
}

#popupMenu.visible {
    transform: scale(1);
    transition: transform 200ms ease-in-out;
}

#popupMenu .item:hover {
    background: #343434;
}

.popupmenu {
    position: absolute;
    list-style-type: none;
    border-color: #aecff7 !important;
    border-style: solid !important;
    background: lightgrey;
}

.popupmenu-item {
    cursor: pointer;
}

.popupmenu-item:hover {
    background-color: white;
}

.popupmenu-item-separator {
    padding: 1px;
    margin: 1px;
    background-color: black;
}